body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	/* font: inherit; */
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Open Sans"; }

.tiny-gutter-right {
  margin-right: 10px; }

.small-gutter-right {
  margin-right: 20px; }

.max-width {
  width: 100%; }

.max-500pixels {
  max-width: 500px; }

.margin-right-auto {
  margin-right: auto; }

.sixteen-px {
  height: 16px;
  width: 16px; }

input {
  padding: 0;
  border: 0;
  background-color: #c9c9c9;
  color: black; }

textarea {
  padding: 0.5em;
  border: 0;
  background-color: #c9c9c9;
  font-style: normal;
  color: black;
  resize: none;
  height: 150px;
  width: 90%; }

:root {
  --card-width: 500px; }

.answer-input {
  height: 100px;
  width: 500px; }
  .answer-input-wrapper {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 1em auto; }

.dark-mode {
  min-height: 100vh;
  background-color: #303030; }

.light-mode {
  min-height: 100vh;
  background-color: white; }

.btn--dark {
  background-color: #422642;
  color: #ecbcff;
  padding: 0.5em;
  border: none; }
  .btn--dark--last {
    background-color: #203ea0;
    color: #ecbcff;
    padding: 0.5em;
    border: none; }
    .btn--dark--last:hover {
      cursor: pointer; }
  .btn--dark--next {
    background-color: #1c3792;
    color: #ecbcff;
    padding: 0.5em;
    border: none; }
    .btn--dark--next:hover {
      cursor: pointer; }
  .btn--dark:hover {
    background-color: #583358;
    cursor: pointer; }

.btn--light {
  background-color: #a1a1a1;
  padding: 0.5em;
  color: black;
  border: none; }
  .btn--light--last {
    background-color: #a1a1a1;
    padding: 0.5em;
    color: black;
    border: none; }
  .btn--light--next {
    background-color: #a1a1a1;
    padding: 0.5em;
    color: black;
    border: none; }
  .btn--light:hover {
    cursor: pointer; }

.btn--colorless {
  background-color: white;
  color: black;
  padding: 0.5em;
  border: none; }

.btn--big {
  font-size: 1.25em;
  font-weight: 400;
  width: 50%; }

.btn--top-right {
  position: absolute;
  background-color: transparent;
  padding: 0;
  border: none;
  top: -15px;
  right: -15px; }

.btn--green {
  border: 0;
  height: 40px;
  width: 25%;
  font-size: 1em;
  font-weight: 400;
  color: #aaffaa;
  background-color: rgba(28, 139, 28, 0.7); }

.btn--red {
  border: 0;
  height: 40px;
  width: 25%;
  font-size: 1em;
  font-weight: 400;
  color: #ffb0b0;
  background-color: rgba(255, 0, 0, 0.7); }

.btn:hover {
  cursor: pointer; }

.testing {
  background-color: transparent;
  height: 32px;
  width: 32px; }

.no-background-color {
  background-color: #54106e; }

.btns {
  display: flex;
  flex-grow: 1;
  width: 100%;
  max-width: 500px;
  margin: 0 auto; }

.btn-wrapper {
  display: flex;
  justify-content: center;
  margin: 0 auto; }

.cards {
  display: flex;
  color: white;
  background-color: rgba(80, 97, 250, 0.75);
  width: 100%;
  max-width: 500px;
  flex-direction: column;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  margin: 2em auto; }

.dimmer {
  height: 100%;
  width: 100%;
  opacity: 0.5;
  background: #000000;
  position: fixed;
  z-index: 100; }

.success {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1em auto;
  border-radius: 5px;
  width: 95%;
  text-align: center;
  color: white;
  background-color: #4bb543;
  height: 50px; }
  .success-message {
    font-size: 1em;
    font-weight: 400; }

.small-title {
  width: 100%;
  max-width: 500px;
  margin: 0.5em 0;
  color: white; }

.tiny-title {
  font-weight: 500;
  margin-bottom: 0.5em;
  font-size: 1.75em; }

.tiny-text {
  font-style: oblique;
  font-weight: 400;
  line-height: 1.5em;
  width: 80%;
  margin: 1em auto; }

.margin-auto-center {
  margin: 0 auto;
  outline: none;
  /* max-width: fit-content; */ }

.cards--dark {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.card--dark {
  background-color: white;
  width: 150px;
  margin: 0 1em;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.15); }

.card--light {
  background-color: #2e2e2e;
  color: white;
  width: 150px;
  margin: 0 1em;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.15); }

.big-card {
  position: relative;
  width: 100%;
  max-width: 500px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0 auto; }

.big-title {
  font-weight: 500;
  font-size: 1.25em;
  color: inherit;
  background-color: inherit; }

.current {
  background-color: white;
  width: 150px;
  margin: 0 1em;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.15); }

.question--dark {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0.5em;
  font-size: 1.25em;
  color: #ecbcff;
  background-color: #54106e; }

.placeholder--200px {
  background-color: inherit;
  height: 100px; }

.answer--dark {
  min-height: 200px;
  width: 100%;
  padding: 0.5em;
  font-size: 1.25em;
  line-height: 1.75em;
  font-weight: 400;
  color: #dc88fd;
  background-color: #54106e; }

.question--light {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5em;
  height: 100%;
  font-size: 1.25em;
  color: #54106e;
  background-color: #ecbcff; }

.answer--light {
  min-height: 200px;
  padding: 0.5em;
  height: 100%;
  font-size: 1.25em;
  font-weight: 400;
  color: #54106e;
  background-color: #ecbcff; }

.slide-wrapper {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto; }

.slide {
  height: 30px;
  background-color: inherit; }

.slide-text {
  font-family: "Open Sans";
  font-weight: 400;
  font-size: 1em;
  color: white; }

.tab {
  width: 50%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: #ebebeb;
  height: 35px;
  padding: 0;
  border: 0; }

.clicked {
  background-color: #b6b6b6;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.3); }

.wrapper {
  width: 100%;
  max-width: 500px;
  margin: 0 auto; }

.small-icon {
  height: 32px;
  width: 32px;
  margin-left: 5px; }

